<template>
    <dynamic-form :formList="formLists" :formModel="formModel" :formOption="formOption" v-if="formModel && formOption"></dynamic-form>
</template>

<script>
import * as _ from 'underscore'
import DynamicForm from '@/components/DynamicForm'
import { Validator } from '@/assets/js/validator'
const validators = new Validator()
export default {
    components: {
        DynamicForm
    },
    data() {
        return {
            formLists: [],
            formOption: null,
            formModel: null
        }
    },
    mounted() {
        this.getFormDatas()
        this.generateFormModel()
        this.generateOptions()
    },
    methods: {
        getFormDatas() {
            var datas = [
                {
                    key: 'name',
                    type: 'text',
                    label: '',
                    model: '',
                    required: true,
                    minLength: 2,
                    maxLength: 20,
                    placeholder: ''
                },
                {
                    key: 'age',
                    type: 'number',
                    label: '',
                    model: '',
                    postfix: '',
                    required: true,
                    min: 1,
                    max: 150,
                    placeholder: ''
                },
                {
                    key: 'phone',
                    type: 'number',
                    label: '',
                    model: '',
                    required: true,
                    minLength: 11,
                    maxLength: 11,
                    placeholder: ''
                },
                {
                    key: 'introduce',
                    type: 'textarea',
                    label: '',
                    model: '',
                    rows: 4,
                    placeholder: '',
                    required: true
                },
                {
                    key: 'email',
                    type: 'email',
                    label: '',
                    model: '',
                    required: true,
                    placeholder: ''
                },
                {
                    key: 'hiredate',
                    type: 'date',
                    label: '',
                    model: '',
                    placeholder: '',
                    required: true
                },
                {
                    key: 'workStartTime',
                    type: 'time',
                    label: '',
                    model: '',
                    placeholder: '',
                    required: true
                },
                {
                    key: 'workEndTime',
                    type: 'time',
                    label: '',
                    model: '',
                    placeholder: '',
                    required: true
                },
                {
                    key: 'birthday',
                    type: 'datetime',
                    label: '',
                    model: '',
                    placeholder: '',
                    required: true
                },
                {
                    key: 'nation',
                    type: 'select',
                    label: '',
                    model: '',
                    placeholder: '',
                    options: [
                        {
                            value: 1,
                            label: ''
                        },
                        {
                            value: 2,
                            label: ''
                        },
                        {
                            value: 3,
                            label: ''
                        },
                        {
                            value: 4,
                            label: ''
                        }
                    ],
                    required: true
                },
                {
                    key: 'sex',
                    type: 'radio',
                    buttonLike: false,
                    label: '',
                    model: '',
                    options: [
                        {
                            value: 1,
                            label: ''
                        },
                        {
                            value: 2,
                            label: ''
                        }
                    ],
                    required: true
                },
                {
                    key: 'hobby',
                    type: 'checkbox',
                    label: '',
                    model: [],
                    options: [
                        {
                            value: 1,
                            label: ''
                        },
                        {
                            value: 2,
                            label: ''
                        },
                        {
                            value: 3,
                            label: ''
                        }
                    ],
                    required: true
                },
                {
                    label: '',
                    type: 'file'
                }
            ]
            _.each(datas, item => {
                if (item.type == 'checkbox') {
                    // checkboxlabelvaluelabellabeloptionslabelcheckbox
                    item.originalOptions = _.clone(item.options)
                    item.options = _.pluck(item.options, 'label')
                }
            })
            this.formLists = datas
            console.log('formLists: ', this.formLists)
        },
        generateFormModel() {
            var obj = {}
            this.formLists.forEach((value, index) => {
                obj[value.key] = value.model
            })
            this.formModel = obj
            console.log('formModel: ', this.formModel)
        },
        generateOptions() {
            var generateRules = {}
            this.formLists.forEach((item, index) => {
                var propRule = []
                // 
                if (item.required) {
                    if (item.type == 'select' || item.type == 'radio' || item.type == 'checkbox') {
                        propRule.push({ required: true, message: `${item.label}`, trigger: 'change'})
                    } else {
                        propRule.push({ required: true, message: `${item.label}`, trigger: 'blur'})
                    }
                }
                if (item.minLength && item.maxLength && item.minLength == item.maxLength) {
                    propRule.push({ min: item.minLength, max: item.maxLength, message: `${item.label}${item.minLength}`, trigger: 'blur'})
                } else {
                    if (item.minLength) {
                        propRule.push({ min: item.minLength, message: `${item.label}${item.minLength}`, trigger: 'blur'})
                    }
                    if (item.maxLength) {
                        propRule.push({ max: item.maxLength, message: `${item.label}${item.maxLength}`, trigger: 'blur'})
                    }
                }
                // number
                if (item.type == 'number') {
                    var hasMax = _.isNumber(item.max)
                    var hasMin = _.isNumber(item.min)
                    if (hasMax || hasMin) {
                        var rangeValidator = (rule, value, callback) => {
                            if (hasMax && value > item.max) {
                                callback(new Error(`${item.label}${item.max}`))
                            } else if (hasMin && value < item.min) {
                                callback(new Error(`${item.label}${item.min}`))
                            } else {
                                callback()
                            }
                        }
                        propRule.push({ validator: rangeValidator, trigger: 'blur'})
                    }
                }
                // email
                if (item.type == 'email') {
                    var emailValidator = (rule, value, callback) => {
                        if (!validators.isEmail(value)) {
                            callback(new Error(''))
                        } else {
                            callback()
                        }
                    }
                    propRule.push({ validator: emailValidator, trigger: 'blur'})
                }
                generateRules[item.key] = propRule
            })
            this.formOption = {
                labelWidth: '120px',
                labelPosition: 'right',
                rules: generateRules
            }
            console.log('formOption: ', this.formOption)
        }
    }
}
</script>

<style lang="scss" scoped>
</style>




