<template>
    <el-row :gutter="20">
        <el-col :span="12" v-for="(column, index) in columnList" :key="index">
            <el-card class="box-card card-column">
                <div slot="header" class="clearfix">
                    <span>{{column.columnName}}</span>
                </div>
                <commodity-list :commodities="column.commodityList">
                    <template slot-scope="scope">
                        <!-- CommodityCommodityclickCommodity
                         -->
                        <commodity :modityData="scope.row" @clickCommodity="onCommodityClick(scope.row)"></commodity>
                    </template>
                </commodity-list>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
import Commodity from '@/components/Commodity'
import CommodityList from '@/components/CommodityList'
export default {
    components: {
        Commodity, CommodityList
    },
    data() {
        return {
            columnList: [
                {
                    columnName: '',
                    commodityList: [
                        {
                            commodityName: '6',
                            detail: '66666666'
                        },
                        {
                            commodityName: 'iphone8',
                            detail: 'iphone8iphone8iphone8iphone8iphone8iphone8iphone8'
                        },
                        {
                            commodityName: '8',
                            detail: '888888888'
                        },
                        {
                            commodityName: '9',
                            detail: '999999999'
                        },
                        {
                            commodityName: 'iphone X',
                            detail: 'iphone Xiphone Xiphone Xiphone Xiphone Xiphone Xiphone X'
                        },
                        {
                            commodityName: '8',
                            detail: '8888888'
                        }
                    ]
                },
                {
                    columnName: '',
                    commodityList: [
                        {
                            commodityName: '',
                            detail: ''
                        },
                        {
                            commodityName: '',
                            detail: ''
                        },
                        {
                            commodityName: '',
                            detail: ''
                        },
                        {
                            commodityName: '',
                            detail: ''
                        },
                        {
                            commodityName: '',
                            detail: ''
                        },
                        {
                            commodityName: '',
                            detail: ''
                        }
                    ]
                }
            ]
        }
    },
    methods: {
        onCommodityClick(row) {
            console.log(row)
        }
    }
}
</script>

<style lang="scss" scoped>

</style>



