<template>
    <section>
        <video ref="video" id="video" class="user-camera" autoplay></video>
        <el-button @click="controlUserCamera(true)"></el-button>
        <el-button @click="controlUserCamera(false)"></el-button>
		<p>navigator.getUserMedia</p>
    </section>
</template>

<script>
export default {
    data(){
        return {
            mediaStreamTrack: null
        }
    },
    methods: {
        controlUserCamera(openFlag) {
            if(openFlag === true) {
				navigator.getUserMedia = navigator.getUserMedia ||  
				navigator.webkitGetUserMedia ||  
				navigator.mozGetUserMedia;  
				if (navigator.getUserMedia) {  
					navigator.getUserMedia({ video: true }, (stream) => {
						console.log(stream)
						this.mediaStreamTrack = stream.getTracks()[0]
						this.$refs.video.src = (window.URL || window.webkitURL).createObjectURL(stream);
						this.$refs.video.play();
					}, (err) => {
						this.$message({message: '', type: 'error'})
						console(": " + err.name);  
					});  
				} else {
					this.$message({message: '', type: 'error'})
					console.log("");
				} 
			} else {
				if (this.mediaStreamTrack) {
					this.mediaStreamTrack.stop()
					this.mediaStreamTrack = null
				}
			}
        }
	},
	beforeDestroy() {
		if (this.mediaStreamTrack) {
			this.mediaStreamTrack.stop()
			this.mediaStreamTrack = null
		}
	}
}
</script>

<style lang="scss" scoped>
    .user-camera {
        width: px2rem(300);
    }
</style>



