function inView(element) {
    // + 20 
    return ($(window).height() + $(window).scrollTop() + 20) >= $(element).offset().top
}

export default {
    bind() {

    },
    inserted(el, binding, vNode, oldVnode) {
        if(inView(el)) {
            el.setAttribute('src', el.getAttribute('data-src'))
        }
        $(window).on('scroll', function() {
            if(inView(el)) {
                el.setAttribute('src', el.getAttribute('data-src'))
            }
        })
    }
}


