<template>
    <section>
        <p></p>
        <el-tree :data="addAuthorities" show-checkbox node-key="id" ref="addtree" highlight-current :props="defaultProps">
        </el-tree>
        <el-button @click="getSelectedNodes"></el-button>
        <el-button @click="getSelectedKeys">key</el-button>
        <p>{{selectedValue}}</p>
        <p>setCheckedNodessetCheckedKeys1setCheckedKeys['1','1-1','1-1-1','1-2','1-2-1'];setCheckedNodes</p>
        <el-button @click="setValueManual"></el-button>
        <el-tree :data="editAuthorities" show-checkbox node-key="id" ref="edittree" highlight-current :props="defaultProps">
        </el-tree>
    </section>
</template>

<script>
import * as _ from 'underscore';
export default {
    data(){
        return {
            selectedValue: '',
            addAuthorities: [],
            defaultProps: {
                children: 'children',
                label: 'name'
            },
            editAuthorities: []
        }
    },
    mounted() {
        this.addAuthorities = [
            {
                id: '1',
                name: '1',
                children: [
                    {
                        id: '1-1',
                        name: '1-1',
                        children: [
                            {
                                id: '1-1-1',
                                name: '1-1-1'
                            }
                        ]
                    },
                    {
                        id: '1-2',
                        name: '1-2',
                        children: [
                            {
                                id: '1-2-1',
                                name: '1-2-1'
                            }
                        ]
                    }
                ]
            },
            {
                id: '2',
                name: '2',
                children: [
                    {
                        id: '2-1',
                        name: '2-1',
                        children: [
                            {
                                id: '2-1-1',
                                name: '2-1-1'
                            }
                        ]
                    },
                    {
                        id: '2-2',
                        name: '2-2',
                        children: [
                            {
                                id: '2-2-1',
                                name: '2-2-1'
                            }
                        ]
                    },
                    {
                        id: '2-3',
                        name: '2-3',
                        children: [
                            {
                                id: '2-3-1',
                                name: '2-3-1'
                            }
                        ]
                    }
                ]
            }
        ]
    },
    methods: {
        getSelectedNodes() {
            this.selectedValue = JSON.stringify(this.$refs.addtree.getCheckedNodes())
        },
        getSelectedKeys() {
            this.selectedValue = JSON.stringify(this.$refs.addtree.getCheckedKeys())
        },
        setValueManual() {
            this.editAuthorities = _.clone(this.addAuthorities)
            this.$refs.edittree.setCheckedKeys(['1','1-1','1-1-1','1-2','1-2-1'])
        }
    },
}
</script>



