<template>
    <section>
        <el-input v-model="svghtml"></el-input>
        <el-input v-model="width"></el-input>
        <el-input v-model="height"></el-input>
        <el-button @click="generateSVG">SVG</el-button>
        <el-button @click="generateImg"></el-button>
        <div id="svgContainer" :style="{width: `${width}px`, height: `${height}px`}"></div>
    </section>
</template>

<script>
import * as _ from 'underscore';
export default {
    data(){
        return {
            svghtml: '',
            width: 400,
            height: 300
        }
    },
    methods: {
        generateSVG() {
            var svgdom = $(this.svghtml);
            $('#svgContainer').html(svgdom);
        },
        generateImg() {
            var image = new Image();
            image.onload = function() {
                var canvas = document.createElement('canvas');
                canvas.width = $('svg').width();
                canvas.height = $('svg').height();

                var context = canvas.getContext('2d');
                context.drawImage(image, 0, 0);

                var a = document.createElement('a');
                a.href = canvas.toDataURL('image/png');
                a.download = "svg";
                a.click();
            }
            image.src = 'data:image/svg+xml;base64,' + window.btoa(unescape(encodeURIComponent(this.svghtml)));
        }
    }
}
</script>

<style lang="scss" scoped>

</style>



