<template>
    <section>
        <el-button @click="changeLang"></el-button>
        <div>{{$i18n.locale == 'en' ? '' : ''}}</div>
        <div>{{$t('helloworld')}}</div>
        <div>
            <el-input v-model="name" type="text" class="j-input"></el-input>
            {{$t('helloman', {name: name})}}</div>
        <div>
            <el-select v-model="iphoneIndex">
                <el-option v-for="iphone in iphoneList" :key="iphone" :value="iphone" :label="iphone"></el-option>
            </el-select>
            {{$tc('iphones', iphoneIndex, {n: '3'})}}</div>
    </section>
</template>

<script>
import * as _ from 'underscore';
export default {
    data(){
        return {
            name: '',
            iphoneList: [0, 1, 2],
            iphoneIndex: 0
        }
    },
    methods: {
        changeLang() {
            if (this.$i18n.locale == 'en') {
                this.$i18n.locale = 'zh'
                this.$store.commit('updateLang', 'zh')
            } else {
                this.$i18n.locale = 'en'
                this.$store.commit('updateLang', 'en')
            }
        }
    }
}
</script>

<style lang="scss" scoped>

</style>



