
import * as _ from 'underscore'

class MyUtils {
    constructor() {}

    loadScript(src, parentNode = document.body, isCheckRepeat = true, replace = false) {
        return new Promise((resolve, reject) => {
            if (isCheckRepeat) {
                let targetScript = _.find([...document.scripts], item => {
                    return src == item.src;
                })
                if (targetScript) {
                    if (replace) {
                        targetScript.parentNode.removeChild(targetScript);
                    } else {
                        // 2script
                        return resolve(2);
                    }
                }
            }
            let script = document.createElement("script");
            script.type = "text/javascript";
            script.src = src;
            parentNode.appendChild(script);
            // onloadonreadystatechangeieonloadfirefoxonreadystatechange
            script.onload = script.onreadystatechange = function(){
                if (!this.readyState || this.readyState == 'loaded' || this.readyState == 'complete') {
                    // 1script
                    resolve(1);
                }
            }
            script.onerror = (event) => {
                reject(event);
            }
        });
	}
}

export default new MyUtils()


