<template>
    <section>
        <div style="margin:0.2rem 0;">index.htmljs</div>
        <div id="allmap" style="height:600px;"></div>
    </section>
</template>

<script>
export default {
    data(){
        return {

        }
    },
    methods: {
        initMap() {
            // Map
            var map = new BMap.Map("allmap");
            // ,
            // 
            map.centerAndZoom(new BMap.Point(112.889, 28.237), 15);
            // 
            map.addControl(new BMap.MapTypeControl({
                mapTypes:[
                    BMAP_NORMAL_MAP,
                    // BMAP_SATELLITE_MAP,
                    BMAP_HYBRID_MAP
                ]}));  
            //  
            map.setCurrentCity("");
            //
            map.enableScrollWheelZoom(true);
        }
    },
    mounted() {
        this.initMap();
    }
}
</script>

<style lang="scss" scoped>

</style>




